import { Popover, PopoverBody } from 'reactstrap';
import classnames from 'classnames';
import './form.scss';


const Input = ({
  id,
  type,
  value,
  pop,
  popText,
  onBlur,
  onFocus,
  onChange,
  color,
  placeholder,
}) => (
  <div className='input_main_cont'>
    <div
      className={classnames({
      input_cont: true,
      light: color === 'light',
    })}>
      <label htmlFor={id}></label>
      <input
        id={id}
        value={value}
        name={id}
        type={type}
        onBlur={onBlur}
        onFocus={onFocus}
        onChange={onChange}
        placeholder={placeholder}
      />
      <Popover placement="right" target={id} isOpen={pop}>
        <PopoverBody>
          {popText}
        </PopoverBody>
      </Popover>
    </div>
  </div>
);

export default Input;
